@import "https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700;900&display=swap";
@import "https://fonts.googleapis.com/css2?family=Merriweather:wght@300;400;700;900&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  color: #fff;
  text-decoration: none;
}

li {
  list-style: none;
}

body {
  height: 100vh;
  width: 100%;
  color: #fff;
  background: linear-gradient(to right, #313131, #191818);
  padding: 30px 20px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
}

#app-name {
  text-align: center;
  font-size: 2.5rem;
}

#main {
  max-width: 600px;
  width: 95%;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 35px auto 0;
  display: flex;
}

#main-container {
  width: 100%;
  background: linear-gradient(-120deg, #313131, #191818);
  border-radius: 30px;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 30px 20px;
  display: flex;
  box-shadow: 0 0 10px #000, 0 0 10px #000;
}

#main-container #period {
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

#main-container #period h5 {
  font-size: 1.8rem;
}

#main-container #period p {
  color: #eeb436;
  font-family: Merriweather, serif;
  font-size: .8rem;
}

#main-container #condition {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 5px auto 0;
  font-family: Merriweather, serif;
  font-size: 1.1rem;
  display: flex;
}

#main-container #condition i {
  color: #eeb436;
  margin-right: 5px;
}

#main-container #condition .condition-text {
  font-weight: 300;
}

#main-container #temp-main {
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 10px 0;
  display: flex;
}

#main-container #temp-main .temp {
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  font-family: Merriweather, serif;
  font-size: 5rem;
  font-weight: 900;
  display: flex;
}

#main-container #temp-main .temp span {
  color: #eeb436;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  font-family: Roboto, sans-serif;
  font-size: 1.2rem;
  font-weight: 600;
  display: flex;
  transform: translateY(20px);
}

#main-container #temp-main .temp span span {
  font-size: 2rem;
  transform: translateY(3px);
}

#main-container #temp-main .temp-icon {
  width: 100px;
}

#main-container #temp-main .temp-icon img {
  width: 100%;
}

#main-container #location {
  width: 100%;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-family: Merriweather, serif;
  font-size: 1.1rem;
  font-weight: 300;
  display: flex;
}

#main-container #location i {
  color: #eeb436;
  margin-right: 5px;
}

#ad-info {
  max-width: 400px;
  width: 70%;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  margin: 35px auto 0;
  display: flex;
}

.info-container {
  width: 100%;
  margin-bottom: 50px;
  font-size: .9rem;
}

.info-container p {
  width: 100%;
  cursor: pointer;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  font-family: Merriweather, serif;
  font-size: 1.1rem;
  display: flex;
}

.info-container p i {
  color: #eeb436;
  font-size: 1.3rem;
  transition: all .5s ease-in-out;
}

.info-container p i.show {
  transform-origin: center;
  transform: rotate(180deg);
}

.infos {
  width: 100%;
  height: 0;
  background: linear-gradient(-120deg, #313131, #191818);
  border-radius: 15px;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 0 20px;
  transition: all .5s ease-in-out;
  display: flex;
  overflow: hidden;
  box-shadow: 0 0 10px #0006, 0 0 10px #0006;
}

.infos.show {
  height: 250px;
}

.infos .info {
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  display: flex;
}

.infos .info:not(:last-child) {
  border-bottom: 2px solid #191818;
  margin-bottom: 10px;
  padding-bottom: 5px;
}

.infos .info:first-child {
  padding-top: 10px;
}

.infos .info:last-child {
  padding-bottom: 10px;
}

.infos .info span {
  color: #eeb436;
  font-family: Merriweather, serif;
}

@media screen and (max-width: 450px) {
  #app-name {
    font-size: 2rem;
  }

  #main-container #temp-main .temp {
    font-size: 4rem;
    font-weight: 900;
  }

  #main-container #temp-main .temp span {
    font-size: 1rem;
    transform: translateY(15px);
  }

  #main-container #temp-main .temp span span {
    font-size: 1.8rem;
    transform: translateY(0);
  }

  #main-container #temp-main .temp-icon {
    width: 90px;
  }
}

